      h2 8.1 Partes del odo humano
    p El odo Humano es el encargado de recibir las ondas de presin sonora, en este se encuentra dividido en tres partes odo interno, odo medio y odo externo.
    .row 
      .col-lg-6
        .row.mb-5
          .col-md-12.mb-5.mb-md-0

            LineaTiempoD.color-secundario
              p(numero="1" titulo="Odo interno") Es una compleja estructura interna del odo que comunica directamente con el nervio acstico. Transmite las vibraciones sonoras desde el odo medio a travs del lquido de los conductos semicirculares (rgano del equilibrio), que se unen al vestbulo, conectado con la cclea (rgano de la audicin). Consta de dos partes: el laberinto seo y el laberinto membranoso.<br><br>Tomado de: CORPOCALDAS.(2015)  Mediciones de ruido ambiental y elaboracin del plan de descontaminacin por ruido en villamara, caldas.(p.22)
              p(numero="2" titulo="Odo medio") Est formado por la Cavidad timpnica y por el yunque, martillo y estribo, se articulan entre s y con la membrana timpnica. Las ondas sonoras se transmiten a travs de ellos cuando vibra el tmpano<br><br>Tomado de : CORPOCALDAS.(2015)  Mediciones de ruido ambiental y elaboracin del plan de descontaminacin por ruido en villamara, caldas (p.22).
              p(numero="3" titulo="Odo externo")  Es la estructura externa constituida por la oreja y el conducto auditivo externo. Las ondas sonoras son conducidas a travs del odo externo hasta el odo medio.<br><br>Tomado de : CORPOCALDAS.(2015)  Mediciones de ruido ambiental y elaboracin del plan de descontaminacin por ruido en villamara, caldas (p.22).
