      h2 8.4 Efectos del ruido en la salud humana
    p Los efectos del ruido en la salud dependen especficamente de la exposicin prolongada al ruido ya sea en el da a da o en el puesto de trabajo este puede causar algunos problemas como la hipertensin, enfermedades cardiacas tambin puede afectar, aunque no lo creamos, la lectura, la atencin y la memoria.  
