            i.fas.fa-quote-right  
    p El ruido puede causar otros muchos problemas, pero la principal consecuencia social es el deterioro de la audicin, que produce incapacidad de entender una conversacin en condiciones normales y que est considerado una desventaja social severa. Los efectos ms significativos estn los siguientes 
