      h2 8.5 Medicin de ruido                  
    img(src='@/assets/curso/tema8/img7.jpg', alt='Texto que describa la imagen')
    p.mt-4 Es importante diferenciar que la medicin del ruido que se va a realizar debe establecer un alcance, puesto que el Ruido Ambiental se mide teniendo en cuenta un rea determinada por su impacto (Parques, Zonas Residenciales, Hospitales, entre otros), en cambio la medicin por Emisin de Ruido se mide siempre y cuando est relacionada con la generacin de ruido que puede generar una empresa o proyecto.
    .col-lg-5.colora2.p-2.mt-4
      h4.text-white.mb-0.text-center Procedimiento para la medicin de emisin de ruido
    .row.mt-4
      .col-lg-6
        img(src='@/assets/curso/tema8/img9.png', alt='Texto que describa la imagen')

      .col-lg-6
        h5 Emisin de Ruido.
        .row.mt-3.colort82.p-3
          p Para este caso la medicin por emisin de ruido se aplica directamente a las fuentes industriales dicha medicin se debe establecer una distancia de 1.5 metros a partir de la fachada o permetro especfico del rea a medir en este caso vamos a ver como es el procedimiento para hacer la medicin por emisin de ruido.
          p La medicin de la emisin de ruido se realiza a 1.5 m de la fachada de una edificacin y a 1.20 m a partir del nivel mnimo donde se encuentre instalada la fuente de emisin de ruido
    .row.mt-4.justify-content-center
