            h5 Recuerda!
            p.mb-0 Es importante que las mediciones de Emisin de Ruido siempre deben hacerse en el espacio pblico, a 1.50 m del lmite de la propiedad en que est instalada la fuente. 
    .col-lg-2.colora2.p-2.mt-4
      h4.text-white.mb-0.text-center Antes del muestreo
    p.mt-3 Se debe tener en cuenta qu antes del muestreo se deben realizar algunas actividades previas con el fin de verificar el equipo de medicin (Sonmetro) y revisar tanto el plan de muestreo como las actividades a realizar.
    .row 
