          .col-md-12.mb-5.mb-md-0

            LineaTiempoD.color-primario
              p(numero="1" titulo="Se debe verificar") Se debe verificar tanto el funcionamiento como el correcto estado de los equipos de medicin con sus respectivas partes verificando que los parmetros de medicin estn incorrecto estado y las calibraciones requeridas.
              p(numero="2" titulo="En caso del sonmetro") En caso del sonmetro se debe realizar una calibracin con su respectivo calibrador para verificar que est funcionando respectivamente en caso de no cumplir ser realizar un mantenimiento preventivo incluso uno correctivo
              p(numero="3" titulo="Adicionalmente se verifica ")  Adicionalmente se verifica el instrumento que mide las condiciones meteorolgicas y de velocidad del viento especialmente el anemmetro con el fin de permitir que sus valores sean representativos y especficos al momento hacia la medicin del viento parmetro importante para la medicin del ruido.  (recordemos que la velocidad del viento no debe ser mayor a 3 m/s)
              p(numero="4" titulo="Es importante realizar ")  Es importante realizar una identificacin del espacio u rea de medicin mediante instrumentos de interpretacin geogrfica que permitan verificar el tipo de sector o subsector en donde se va a realizar la medicin.
                           
      .col-lg-6
