              p(numero="5" titulo="Otro instrumento que tambin es importante ") Otro instrumento que tambin es importante verificar es el GPS el cual determina por coordenadas la ubicacin del sitio a medir es importante que no haya desviaciones en el equipo de ms de 3 metros dependiendo de la recepcin de los satlites que tenga incluido dentro de sus configuraciones.
              p(numero="6" titulo="Al momento de descargar") Al momento de descargar los datos que recibe el sonmetro es importante verificar tanto el software cmo la interface que se emplea para descargar los datos ya que es importante almacenar esta informacin una vez terminado en muestreo. 
              p(numero="7" titulo="El transporte y la manipulacin") El transporte y la manipulacin de los equipos durante el proceso de carga es importante verificar que el embalaje sea seguro y especfico para los equipos que se van a llevar a cabo a campo. (incluye maletines, cajas, embalajes, etc)
              p(numero="8" titulo="Tambin es importante verificar ")  Tambin es importante verificar los elementos de proteccin personal qu debe tener presente el personal encargado de la medicin debido a que los riesgos y peligros presentes en los diversos sitios se deben tener en cuenta para evitar accidentes.
