        img(src='@/assets/curso/tema8/img25.png', alt='Texto que describa la imagen')

      .col-lg-5
        .row.p-5.colort81
          p Con el fin de prevenir posibles errores durante la medicin el sonmetro debe estar orientado con la pantalla anti-viento hacia la direccin de la fuente especfica, segn sea el caso nicamente debe estar el tcnico que realiza las mediciones, entre ms personas que estn presentes en el sitio de medicin y ms an cerca al sonmetro, se podra presentar fenmenos como el apantallamiento, que influye negativamente en la recepcin de la informacin por parte del sonmetro.
      figcaption (Fuente imagen: Universidad de Medellin.(2009). Protocolo para la medicin de emisin de ruido, ruido ambiental y realizacin de mapas de ruido. P.28-29
      p.mt-2 La separacin que debe haber entre el tcnico (y las dems personas, si las hubiera en el momento de la medicin) y el sonmetro debe ser de por lo menos 0.5 m.
      .row.justify-content-center.mt-4
        .col-lg-8.mt-4
          img(src='@/assets/curso/tema8/img26.png', alt='Texto que describa la imagen')
          figcaption  Universidad de Medellin.(2009). Protocolo para la medicin de emisin de ruido, ruido ambiental y realizacin de mapas de ruido. P.28-29

    h5.mt-4 Aspectos a tener en cuenta durante la medicin:
    .row.mt-4.align-items-center
      .col-lg-6
        img(src='@/assets/curso/tema8/img26.png', alt='Texto que describa la imagen')

      .col-lg-6
        .row.p-5.colort82
          p * <strong>Es importante</strong> cuando se realice la medicin en el da o en la noche tener presente que las condiciones en donde el proyecto o la empresa est funcionando con la mayor intensidad de ruido con el fin de medir en las condiciones reales de operacin de la fuente.
          p * Se deben realizar por punto (2) procesos de medicin de una hora de captura de informacin o a intervalos de 15 minutos.
          p * Una Medicin (con la fuente de emisin operando en su mximo nivel de produccin o de emisin de ruido) 
      
    .row.mt-2.align-items-center
      .col-lg-6
        img(src='@/assets/curso/tema8/img27.png', alt='Texto que describa la imagen')

      .col-lg-6
        .row.p-5.colort82
          p.mb-0.mt-5 * <strong>Una Medicin</strong>  (con la fuente de emisin en condiciones neutras de operacin o mejor dicho sin la emisin de ruido generado)
          p.mb-5 Todo esto, con el fin de determinar el ruido residual.
    p.mt-3 En caso de presentarse ruidos que no pertenezca a la operacin directa de la fuente como (pitos, altavoces, sirenas, perifoneos, etc) estos no debern ser tenidos en cuenta y la informacin debe ser relacionada en el informe tcnico.
    separador
    #t_8_12.titulo-segundo.color-acento-contenido
      h2 8.12 Parmetros de medicin
    p Vamos a reconocer cuales son los principales parmetros empleados para la medicin de la emisin de Ruido, para ello es importante identificar el siguiente glosario:
    .row.mt-4
      .col-lg-6
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.justify-content-center(titulo="Ruido")
            .col-md-10.mb-4.mb-md-0
              p Sonido con una intensidad alta que interfiere en la comunicacin entre las personas o en sus actividades (dormir, leer, descansar, etc.) y que puede resultar incluso perjudicial para la salud humana. (Protocolo ruido ambiental UM/MADS)
          .row.justify-content-center(titulo="dB(A):")
            .col-md-10.mb-4.mb-md-0
              p Unidad de medida de nivel sonoro con ponderacin frecuencial (A). (Res 627/2006)
          .row.justify-content-center(titulo="Decibel (dB): ")
            .col-md-10.mb-4.mb-md-0
              p Dcima parte del Bel, razn de energa, potencia o intensidad que cumple con la siguiente expresin: Log R = 1dB/10 Donde R= razn de energa, potencia o intensidad (Res 627/2006)
          .row.justify-content-center(titulo="Emisin de Ruido:  ")
            .col-md-10.mb-4.mb-md-0
              p Es la presin sonora que, generada en cualesquiera condiciones, trasciende al medio ambiente o al espacio pblico. (Res 627/2006)
          .row.justify-content-center(titulo="Filtros de Tercios de Octava ")
            .col-md-10.mb-4.mb-md-0
              p dispositivo que permite efectuar anlisis de una la seal acstica, en bandas de tercios de octava. (Res 627/2006)
          .row.justify-content-center(titulo="Leq")
            .col-md-10.mb-4.mb-md-0
              p Nivel sonoro continuo equivalente, es el nivel en dBA de un ruido constante hipottico correspondiente a la misma cantidad de energa acstica que el ruido real considerado, en un punto determinado durante un perodo de tiempo T.( Res 627/2006)
          .row.justify-content-center(titulo="Norma de emisin de ruido")
            .col-md-10.mb-4.mb-md-0
              p Es el valor mximo permisible de presin sonora, definido para una fuente, por la autoridad ambiental competente, con el objeto de cumplir la norma de ruido ambiental. (Res 627/2006)
                                                                 
      .col-lg-6
        img(src='@/assets/curso/tema8/img29.jpg', alt='Texto que describa la imagen')
    .row.mt-4
      .col-lg-6
        img(src='@/assets/curso/tema8/img30.jpg', alt='Texto que describa la imagen')

      .col-lg-6
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.justify-content-center(titulo="Pistfono: ")
            .col-md-10.mb-4.mb-md-0
              p Es una pequea cavidad provista de un pistn con movimiento de vaivn y desplazamiento medible, que permite establecer una presin conocida en el interior de la cavidad. Generalmente utilizado para efectuar calibraciones de sonmetros.
          .row.justify-content-center(titulo="Ruido de fondo")
            .col-md-10.mb-4.mb-md-0
              p Unidad de medida de nivel sonoro con ponderacin frecuencial (A). (Res 627/2006)
          .row.justify-content-center(titulo="Ruido residual")
            .col-md-10.mb-4.mb-md-0
              p Ruido total de todas las fuentes de interferencia en un sistema utilizado para produccin, medida o registro de una seal, independiente de la presencia de la seal, incluye ruido elctrico de los equipos de medida. El ruido de fondo se utiliza algunas veces para expresar el nivel medido cuando la fuente especfica no es audible y, a veces, es el valor de un determinado parmetro de ruido, tal como el L90 (nivel excedido durante el 90% del tiempo de medicin)..( Res 627/2006)
          .row.justify-content-center(titulo="Sonmetro")
            .col-md-10.mb-4.mb-md-0
              p Es un instrumento de medicin de presin sonora, compuesto de micrfono, amplificador, filtros de ponderacin e indicador de medida, destinado a la medida de niveles sonoros, siguiendo unas determinadas especificaciones. Res 627/2006)
          .row.justify-content-center(titulo="Filtros (escalas o curvas) de ponderacin o compensacin frecuencial")
            .col-md-10.mb-4.mb-md-0
              p.mb-0 Son filtros normalizados que permiten ponderar las diversas frecuencias presentes en una seal audible en forma diferencial. En la mayora de las aplicaciones vinculadas a la deteccin de los niveles sonoros y a la estandarizacin de los niveles admisibles para determinadas situaciones, se emplea el filtro de ponderacin A.
              p La escala A presenta una buena correlacin a largo plazo con el dao auditivo y una aceptable correlacin con la sensacin de molestia que puede causar el ruido. (Protocolo ruido ambiental UM/MADS)

          .row.justify-content-center(titulo="ponderacin A")
            .col-md-10.mb-4.mb-md-0
              p Nivel sonoro continuo equivalente, es el nivel en dBA de un ruido constante hipottico correspondiente a la misma cantidad de energa acstica que el ruido real considerado, en un punto determinado durante un perodo de tiempo T.( Res 627/2006)
          .row.justify-content-center(titulo="Nivel percentil 90")
            .col-md-10.mb-4.mb-md-0
              p Es el valor mximo permisible de presin sonora, definido para una fuente, por la a(nivel de permanencia o de excedencia 90 %): Es el nivel sonoro que se sobrepasa durante el 90 % del tiempo de observacin. Se suele utilizar para indicar lo que se conoce como ruido de fondo, que es el nivel de presin sonora mnimo o de base, que est presente casi todo el tiempo. Se denota por L90 (Protocolo ruido ambiental UM/MADS)
    p.mt-3 Teniendo en cuenta la informacin anterior, podemos indicar que los principales parmetros para la medicin de la emisin de ruido son:
    .row.justify-content-center.colort82.p-3
      .col-lg-2
        img(src='@/assets/curso/tema8/img31.svg', alt='Texto que describa la imagen')

      .col-lg-8                                              
        ul.lista-ul--color
          li 
            i.fas.fa-angle-right.colorclaro
            p  <strong>Nivel de presin sonora</strong> continuo equivalente con filtro de ponderacin A, LAeq,T y respuesta lenta (S).
          li 
            i.fas.fa-angle-right.colorclaro
            p  <strong>Ruido residual</strong> , medido como nivel de presin sonora continuo equivalente con filtro de ponderacin A, LAeq,T, Residual y respuesta lenta (S).
          li 
            i.fas.fa-angle-right.colorclaro
            p  <strong>Nivel</strong>  percentil L90.
    p.mt-3 Si por alguna razn no es posible medir el ruido residual, se toma como valor indicativo (o representativo) de ste el correspondiente al nivel percentil L90. Esto se debe hacer constar en el informe tcnico, en el que a su vez se deben especificar las razones por las cuales no fue posible medir el ruido residual 
    separador
    #t_8_13.titulo-segundo.color-acento-contenido
      h2 8.13 Intervalos y tiempos de medicin
    p Cuando se va a realizar la medicin se debe establecer unos tiempos hoy intervalos que permitan tomar muestras caractersticas representativas de acuerdo con lo establecido en la resolucin 627de 2006 el tiempo mximo es de una (1) hora de captura informacin con 15 minutos como mnimo con el fin de evitar incurrir en mediciones cortas que generaran baja representatividad.
    .row.mt-4.justify-content-center
      .col-lg-10
        .row.p-4.colorn.align-items-center
          .col-lg-1
            img(src='@/assets/curso/tema3/1.svg', alt='Texto que describa la imagen')
          .col-lg-11
            h5.mb-0 No, olvides!

            p Que la Resolucin 627 de 2006 establece que la captura de informacin en decibeles para la EMISION DE RUIDO es de una (1) hora y de quince (15) minutos como mnimo, sin embargo, si las condiciones para medir en esos tiempos no se dan la duracin de cada intervalo de tiempo de medicin no podr ser inferior a 5 minutos
            p Cuando la(s) fuente(s) de emisin de ruido, por su naturaleza o modo de operacin, fluctan, en principio no resulta adecuado tomar las mediciones en los intervalos de tiempo mencionados, por lo tanto, se debe adoptar un intervalo de medicin que cubra todas las variaciones significativas de la emisin de ruido de la fuente objeto de estudio.
    separador
    #t_8_14.titulo-segundo.color-acento-contenido
      h2 8.14 Clculos
    .col-lg-2.colora2.p-2.mt-4
      h4.text-white.mb-0.text-center Nivel promedio
    p.mt-3 Obtenga un solo valor con las mediciones de cada intervalo de tiempo, si es que las mediciones se hicieron por intervalos, para el LAeq,1h, LAeq,1h, Residual (o nivel percentil 90) para las mediciones con ponderacin temporal lenta (S), de acuerdo con la siguiente ecuacin:
    .row.justify-content-center
      .col-lg-10
        .row.p-3.colort82.align-items-center
          p En la cual:
          .col-lg-6
            img(src='@/assets/curso/tema8/img32.png', alt='Texto que describa la imagen')

          .col-lg-6
            p.m-0 <strong>LAeq,1h =</strong> Nivel de presin sonora continuo equivalente promedio, dB(A).
            p.m-0 <strong>LAeq,Ti =</strong>  Nivel de presin sonora continuo equivalente de cada evento, dB(A).
            p.m-0 <strong>Ti=</strong>   Intervalo de tiempo del evento individual, min.
    
    p.mt-3 Tenga en cuenta que, si los intervalos de tiempo fueron iguales, la ecuacin anterior se puede simplificar de la siguiente manera: 
    .row.justify-content-center      
      .col-lg-10
        .row.p-3.colort82.align-items-center
          p En la cual:
          .col-lg-6
            img(src='@/assets/curso/tema8/img33.png', alt='Texto que describa la imagen')

          .col-lg-6
            p.mb-0 <strong>N =</strong>  Nmero de mediciones.
    .col-lg-3.colora2.p-2.mt-4
      h4.text-white.mb-0.text-center Calculo emisin o aporte de ruido
    p.mt-3 Al obtener los registros de los decibeles producto de la medicin de ruido durante el monitoreo, el clculo de la emisin o aporte de ruido de la fuente se obtiene al restar logartmicamente, el nivel corregido de presin sonora continuo equivalente total con filtro de ponderacin (A) del nivel corregido de presin sonora continuo equivalente residual con filtro de ponderacin A, del cmo se expresa a continuacin
    .row.justify-content-center
      .col-lg-10
        .row.p-3.colort82.align-items-center.justify-content-center
          p En la cual:
          .col-lg-8
            img(src='@/assets/curso/tema8/img34.png', alt='Texto que describa la imagen')

          .col-lg-12
            p.mb-0 Leq emisin = Nivel de emisin de ruido o aporte de la(s) fuente(s) de emisin de ruido con filtro de ponderacin A, dB(A).  
            p.mb-0  LRAeq,1h = Nivel corregido de presin sonora continuo equivalente total con filtro de ponderacin A, dB(A). 
            p   LRAeq,1h, Residual = Nivel de ruido residual corregido con filtro de ponderacin A (medido como nivel de presin sonora continuo equivalente o como nivel percentil 90), dB(A).
    p.mt-3 El nivel de emisin de ruido, o aporte de la(s) fuente(s) de emisin de ruido, con filtro de ponderacin A (Leq emisin) es el que se compara con la tabla 1 del artculo 9 (Estndares mximos permisibles de emisin de ruido) de la Resolucin 627 del 7 de abril de 2006 del Ministerio de Ambiente, o aqulla que la sustituya, modifique o complemente.
    p Tenga en cuenta que el punto donde se realiz la medicin debe ser localizado, para posteriormente reconocerlo e identificarlo para generar las respectivas observaciones que se deriven de los anlisis que se concluyan posterior a la medicin. 
    .row.mt-4.justify-content-center
      .col-lg-10
        .row.p-4.colorn.align-items-center
          .col-lg-1
            img(src='@/assets/curso/tema3/1.svg', alt='Texto que describa la imagen')
          .col-lg-11
            h5.mb-0 Para tener en cuenta!
            p Si la diferencia aritmtica entre LRaeq,1h y LRaeq,1h, Residual es igual o inferior a 3 dB(A), se deber indicar que el nivel de emisin de ruido es del orden igual o inferior al ruido residual (LRaeq,1h, Residual). En este caso se considera que el nivel de ruido residual es demasiado alto para establecer claramente cul es la contribucin de la fuente.
            p Esto no implica que la fuente de emisin de ruido objeto de estudio est exenta del cumplimento de la norma, sino que se debe realizar un estudio exhaustivo de la fuente y su entorno, debido a la incertidumbre tan alta de la medicin, para poder determinar apropiadamente la emisin de ruido.
