    #t_8_15.titulo-segundo.color-acento-contenido
      h2 8.15 Finalizacin del muestreo de emisin de ruido      
    p Cuando se finaliza el muestreo y una vez recogido los datos por punto teniendo en cuenta que deben cuantificar una hora por punto medicin se debe realizar una calibracin final empleando el pistfono, esto permite verificar que la medicin cumpla con las condiciones de fiabilidad presentes durante la medicin.
    .row.justify-content-center.mt-3
      .col-lg-10
        .row.align-items-center.justify-content-center
          .col-lg-5
            img(src='@/assets/curso/tema8/img35.png', alt='Texto que describa la imagen')
          .col-lg-7
            p Los datos recolectados se deben registrar en las hojas de campo y adicional bajar los datos con el software integrado en el sonmetro a una hoja de clculo dando as la confirmacin de los datos tanto en fsico como en digital para posteriormente realizar los respectivos informes de campo.
            p Tambin es importante que durante la medicin se debe generar registro fotogrfico por cada punto de medicin y registrar los datos de la medicin del viento en el muestreo que se llev al lugar.
    .row.mt-4.justify-content-center
      .col-lg-10
        .row.p-4.colorn.align-items-center
          .col-lg-1
            img(src='@/assets/curso/tema3/1.svg', alt='Texto que describa la imagen')
          .col-lg-11
            h5.mb-0 Para recordar!
            p Te presento la ruta del muestreo o medicin, este esquema te ayudar a sintetizar las etapas de la medicin para realizar la Emisin de ruido
            p Apyate en esta para la realizacin de los muestreos de ruidoAnmate a conocer ms!!!!
    separador
    #t_8_16.titulo-segundo.color-acento-contenido
      h2 8.16 La ruta de la medicion
    p Ruta de medicin para la emisin de ruido:
    .row.justify-content-center.mt-3
      .col-lg-10
        .row 
          .col-lg-6
            .row.mb-5
              .col-md-12.mb-5.mb-md-0

                LineaTiempoD.color-secundario
                  p(numero="1" titulo="Antes de emprender una medicin") Antes de emprender una medicin para determinar la emisin de ruido es necesario verificar las condiciones meteorolgicas. (Verificar la velocidad del viento no debe ser mayor a 3m/s).
                  p(numero="2" titulo="El sonmetro se ubica con el micrfono") El sonmetro se ubica con el micrfono ubicado hacia la fuente a 1.5 de la fachada y a 1.20 de altura, separado del operador del equipo a 50cm para evitar el apantallamiento del sonido.
                  p(numero="3" titulo="Realizar calibracin acstica ")  Realizar calibracin acstica del sonmetro con el pistofono antes de iniciar la medicin.
                  p(numero="4" titulo="Las mediciones se realizan con")  Las mediciones se realizan con respuestas lentas (s) y con filtros de ponderacin frecuencial A y Z (antes lineal) y con respuesta por impulso (I) y con filtro de ponderacin frecuencial A.
                  p(numero="5" titulo="La medicin deber realizarse en el da ")  La medicin deber realizarse en el da, horario y condiciones de funcionamiento donde la intensidad de la emisin de ruido sea mayor.
                                                      
          .col-lg-6
            img(src='@/assets/curso/tema8/img36.png', alt='Texto que describa la imagen')
        .row.mt-3
                                                      
          .col-lg-6
            img(src='@/assets/curso/tema8/img37.png', alt='Texto que describa la imagen')
          .col-lg-6
            .row.mb-5
              .col-md-12.mb-5.mb-md-0

                LineaTiempoD.color-secundario
                  p(numero="6" titulo="Se deben realizar dos (2) procesos de medicin") Se deben realizar dos (2) procesos de medicin de por lo menos quince (15) minutos de captura de informacin cada uno, uno con la(s) fuentes (s) de emisin de ruido funcionando durante el periodo de tiempo de mayor emisin o incidencia para determinar EL NIVEL DE PRESION SONORA CONTINUA (LAeq, 1h) y otro sin las fuente(s) funcionando, para determianr EL RUIDO RESIDUAL, (LRaeq, 1h, residual)
                  p(numero="7" titulo="El ruido residual") EL RUIDO RESIDUAL (nivel de presin sonora continuo equivalente con filtro de ponderacin A, LRaer, 1h, Residual) se mide con la(s) fuente(s) especifica(s) apagadas(s) y en el mismo sitio de la medicin anterior, manteniendo invariables los condicionantes del entorno y durante el tiempo y forma.
                  p(numero="8" titulo="El nmero mnimo de mediciones a ejecutar es uno")  El nmero mnimo de mediciones a ejecutar es uno (1), el cual consta de dos(2) procesos de medicin de por lo menos quince (15) minutos de captura de informacin cada uno, uno con la(s) fuente(s) de emisin de ruido funcionando y otro con la(s) fuente(s) especificas(s) apagada(s), en intervalos de tiempo distribuidos uniformemente en una hora, en el horario diurno.
                  p(numero="9" titulo="Si durante las mediciones se presentan ruidos ocacionale")  Si durante las mediciones se presentan ruidos ocacionales que no pertenecen a la fuente objeto de evaluacin, como por ejemplo sirenas, altavoces, etc., stos no se deben tener en cuenta.
                  p(numero="10" titulo="Cuando la emisin de ruido en un sector o subsector") Cuando la emisin de ruido en un sector o subsector, trascienda a sectores o subsectores vecinos o inmersos en l, los estndares mximos permisibles de emisin de ruido son aquellos que corresponden al sector o subsector ms restrictivo.
    separador
    #t_8_17.titulo-segundo.color-acento-contenido
      h2 8.17 Olores
    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema8/img38.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Los olores ofensivos se convierten en un problema medioambiental dependiendo el origen y el tipo de fuente que la generan, de esta manera da origen de numerosas quejas que con fundamento produce molestias a la poblacin en determinada rea de influencia.

          br
          br
          | An en el caso de que las sustancias olorosas emitidas no posean ningn efecto perjudicial para la salud, las molestias causadas por olores pueden constituir un serio problema que necesita ser evaluado, investigado en sus causas y solucionado para responder a las quejas de la sociedad. (Fundamentos Bsicos C.A.O 2008 pg.5)
    p.mt-3 En esta seccin veremos los conceptos generales de los olores ofensivos medicin del contaminante y la normatividad asociada para la vigilancia y control de este parmetro contaminante de acuerdo con lo establecido en la resolucin 1541 de 2013 en Colombia.
    separador
    #t_8_18.titulo-segundo.color-acento-contenido
      h2 8.18 El olfato
    p El olfato es uno de los sentidos ms importantes que tiene el ser humano, el olfato es encargado de detectar y procesar los olores que estn presentes en el aire,
    p Todos estos olores penetran por la nariz, especficamente acceden por sus fosas nasales y llegan a la cavidad nasal, donde las clulas receptoras al detectar este olor los transmite al bulbo olfatorio como impulsos (https://www.partesdel.com) los cuales se encargan de enviar seales al cerebro para interprete esta sensacin en un olor.
    p A continuacin, lo invitamos a que ample esta informacin visualizando el siguiente video este video.
