    #t_8_19.titulo-segundo.color-acento-contenido
      h2 8.19 Partes del olfato
    p Para entender cmo funciona el sentido del olfato veremos su estructura y las partes principales. Haga clic en cada parte de la nariz para ver su descripcin
    .row.mt-4.bgt81.p-3
      .row.align-items-center
        .col-lg-6
          img(src='@/assets/curso/tema8/img39.png', alt='Texto que describa la imagen')
        .col-lg-6
          h5.mb-0.colortextoazul Fosas nasales.
          p Se trata de las dos cavidades que se encuentran sobre la boca. Tomado de : partedel.com. (s,f) La Nariz. 
          h5.mb-0.colortextoazul Nervio olfativo.
          p Localizados en el interior de la cavidad craneana donde cada nervio al ensancharse crea un bulbo olfatorio. Este nervio es totalmente sensitivo, es precisamente quien se encarga de transmitir los impulsos olfatorios desde la nariz hasta el sistema nervioso central. Tomado de : partedel.com. (s,f) La Nariz. 
          h5.mb-0.colortextoazul Pituitaria
          p Se encarga de calentar todo el aire que se respira. En esta zona donde estn las clulas olfativas, donde las sustancias odorferas las impresionan. Esta presenta un tono amarillento y se halla en el cornete superior, o sea, en el espacio donde las ramas del nervio olfatorio se distribuyen. Tomado de : partedel.com. (s,f) La Nariz. 
      h5.mb-0.mt-4.colortextoazul Cilios olfatorios
      p.mb-0 Se trata de clulas nerviosas que contienen pelos muy pequeos y visibles solo con un microscopio, las cuales tienen la funcin de acumular todos los olores para que as los enve el receptor hacia el encfalo, lo cual lo realiza en forma de impulso nervioso. 
      p Tomado de : partedel.com. (s,f) La Nariz. 
      h5.mb-0.colortextoazul Bulbo olfativo
      p.mb-0 Tambin se le conoce como lbulo olfativo. Se trata de una parte del cerebro donde todas seales sensoriales son interpretadas.
      p Refiere a la parte interior de la nariz, la cual est cubierta por una pared compuesta de mucosa, encargada de producir el moco. Esta se encarga de mantener la humedad en la cavidad nasal para as poder impedir la entrada de suciedad, polvo y de ciertas partculas que pueden causar dao si acceden a los pulmones. 
      h5.mb-0.colortextoazul Nariz
      p.mb-0 Posee dos compartimientos que se encuentran divididos por el tabique nasal, donde se muestran dos orificios de salida que se le conoce como narinas. En su otro lado este rgano finaliza en unas aberturas que tienen conexin directa con la faringe.
      p Cada fosa nasal posee en sus paredes laterales unos huesos esponjosos que se le conoce como cornetes. Bajo estos existe un espacio que mantiene la conexin de la nariz y los senos paranasales, lo cual se conoce como meatos. 
      h5.mb-0.colortextoazul Receptores olfativos
      p Estos se hallan en la parte superior de las fosas nasales, aqu es donde el cornete superior es cubierto por la pituitaria amarilla y se crea la conexin con el bulbo olfatorio, estos tienen como funcin detectar los olores. 
      h5.mb-0.colortextoazul Narinas
      p Se trata de dos cavidades que se mantienen separadas a travs de un tabique. Se ubican en la cabeza, justo sobre la boca. 
