                  p(numero="1" titulo="OLOR:")  propiedad organolptica perceptible por el rgano olfativo cuando inspira determinadas sustancias voltiles. (Resolucin 1541 de 2013)
                  p(numero="2" titulo="OLOR OFENSIVO:")  es el olor generado por sustancias o actividades industriales, comerciales o de servicio, que produce fastidio, aunque no cause dao a la salud humana. (Decreto 948 de1995)
                  p(numero="3" titulo="Emisin") transferencia de contaminantes de la atmsfera a un receptor. Se entiende por inmisin la accin opuesta a la emisin. Aire inmiscible es el aire respirable al nivel de la tropsfera. (Decreto 948 de 1995)
                  p(numero="4" titulo="SUSTANCIA DE OLOR OFENSIVO:")   es aquella que, por sus propiedades organolpticas, composicin y tiempo de exposicin puede causar olores desagradables. (Resolucin 1541 de 2013)
                  p(numero="5" titulo="EMISIN:")   transferencia de contaminantes de la atmsfera a un receptor. Se entiende por inmisin la accin opuesta a la emisin. Aire inmiscible es el aire respirable al nivel de la tropsfera. (Decreto 948 de 1995)
                                                      
