                LineaTiempoD.color-secundario
                  p(numero="6" titulo="Umbral de molestia:")   Es la concentracin a la que una pequea parte de la poblacin (< 5%) manifiesta molestias durante un periodo corto de tiempo. Dado que la sensacin de molestia puede estar influenciada por factores sicolgicos y socioeconmicos un umbral de molestia no puede definirse solo con base en la concentracin.
                  p(numero="7" titulo="Umbral de tolerancia")  es el olor generado por sustancias o actividades industriales, comerciales o de servicio, que produce fastidio, aunque no cause dao a la salud humana. (Decreto 948 de1995)
                  p(numero="8" titulo="Unidad de olor:")   es la cantidad de (una mezcla de) sustancias olorosas presentes en un metro cbico de gas oloroso (en condiciones normales 1 atmsfera de presin y 0 C de temperatura) origina una respuesta fisiolgica en el umbral del panel. (Resolucin 1541 de 2013)
                  p(numero="9" titulo="Unidad de olor Europea:")   cantidad de sustancia olorosa qu coma cuando se evapora en 1 metro cbico de gas neutro en condiciones normales, origina una respuesta fisiolgica de un panel (umbral de deteccin) equivalente al que origina una masa de olor de referencia europea more evaporada en 1 metro cbico de gas neutro en condiciones normales. (Resolucin 1541 de 2013)
                  p(numero="10" titulo="Olfatometra:")    tcnica sensorial de medicin de olores que se usa para determinar el grado de molestia que pueden ocasionar ciertos olores a la poblacin.
