        p En la tabla de sustancias de olores ofensivos por actividad resolucin 1541 de 2013 se dnde se clasifican actividades y sectores que aportan contaminacin por olores ofensivos.
        p Es importante no solamente conocer las actividades de generan los olores sino tambin, identificar cules son las sustancias que lo producen, estas sustancias se pueden encontrar en la corriente de aire en forma gaseosa o lquida, tambin en los recursos hdricos y en el suelo.
        p A nivel internacional se han clasificado actividades como las principales generadoras de olores ofensivos y por esta razn explicaremos particularmente su incidencia en la generacin de olores ofensivos, que se visualizan en la tabla de aspectos ambientales de las sustancias asociadas a olores ofensivos.
