                h5 Plantas de tratamiento de agua residual
                p Las plantas de tratamiento de agua residual (PTAR) que tenga fallas en su diseo u operacin, sea de tipo fsico, qumico o biolgico, puede generar malos olores. El medio anaerobio es el ms susceptible a esta situacin, debido al metabolismo de las bacterias sulfato-reductoras, especialmente si en el agua residual existen altas concentraciones de sulfatos y sulfuros. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p. 24)

              .col-md-3
                figure
                  img(src='@/assets/curso/tema8/img45.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-lg-11
            .row.align-items-center
              .col-md-9.mb-4.mb-md-0
                h5 Rellenos Sanitarios. 
                p Desde la aparicin de las ciudades han existido problemas de salud pblica por la generacin de residuos, Los olores ofensivos provenientes de los rellenos sanitarios generalmente ocurren por un mal funcionamiento de los biofiltros o prcticas inadecuadas, como reutilizacin de reas previamente utilizadas o por descargas incontroladas de residuos. Esta percepcin de los malos olores no es permanente, sino que su presencia est asociada a las condiciones del clima y a la direccin de los vientos, siendo las horas nocturnas y los das clidos que siguen a las lluvias, cuando los olores son ms intensos.  (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.28)

              .col-md-3
                figure
                  img(src='@/assets/curso/tema8/img46.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-lg-11
            .row.align-items-center
              .col-md-9.mb-4.mb-md-0
                h5 Curtido de Cuero.
                p las industrias de curtido y acabado de cueros, tradicionalmente han aparecido ante el mundo como algunas de las ms sucias y contaminantes denido al proceso para que este producto tenga el terminado que conocemos comercialmente, como consecuencia El agua residual de la industria de curtido tiene altas concentraciones de materia orgnica,compuestos de nitrgeno, sulfuros, slidos suspendidos, compuestos de cromo y pH elevado.
                p La alta carga de materia orgnica provoca la creacin de condiciones anaerobias en cuerpos o cursos de agua, debido al elevado consumo de oxgeno disuelto. Estas condiciones, adems de afectar la vida acutica, favorecen la produccin de algunos gases nocivos como cido sulfhdrico y metano indicadores cualitativos para la generacion de olores ofensivos  de muy mal carcter. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p. 30) 
                p.mb-0 Generalmente los malos olores provienen de un inadecuado control en las diferentes actividades , las mas significativas son:
                p El pelambre, descarne, la limpieza de equipos y recipientes y el vertimiento de efluentes lquidos y slidos a los cursos de agua.


              .col-md-3
                figure
                  img(src='@/assets/curso/tema8/img47.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-lg-11
            .row.align-items-center
              .col-md-9.mb-4.mb-md-0
                h5 Industria petroqumica.
                p En la industria petrolera, los yacimientos de crudo contienen contaminantes entre los cuales se encuentra el azufre, el cual al combinarse con molculas de hidrogeno forma el cido sulfhdrico, que es liberado de los hidrocarburos en forma de gas al suministrarles calor. Existen microorganismos anaerobios capaces de utilizar compuestos de azufre, tales como las bacterias sulfato reductoras (BSR) y las bacterias tiosulfato reductoras (BSTR), las cuales utilizan el azufre como aceptores finales de electrones en la oxidacin de la materia orgnica, produciendo principalmente cido sulfhdrico y mercaptanos. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.30).
                p Otro aspecto importante que tiene que ver con la industria petrolera y la contaminacin por olores ofensivos son los derrames de petrleo debido a la ocurrencia de accidentes o atentados a la infraestrucctra petrolera en nuestro pais influyendo en la salud de las poblaciones afectadas.


              .col-md-3
                figure
                  img(src='@/assets/curso/tema8/img48.png', alt='Texto que describa la imagen')
