      h2 8.23 Efectos de los olores ofensivos en la salud humana
    p Un olor que en principio es agradable cuando es percibido con tanta intensidad puede convertirse en molesto, es por eso que el concepto de olor ofensivo tambin se vuelve muy subjetivo, debido a que los olores dependen especficamente de la intensidad o fuerza y de la concentracin en aire del compuesto(s) que lo origina.
    p As como el ruido, las vibraciones y la luz, el olor es considerado un estresor ambiental. Segn la Organizacin Mundial de la Salud (OMS) La salud es un estado de completo bienestar fsico, mental y social, y no solamente la ausencia de afecciones o enfermedades. As la molestia por olor afecta la calidad de vida de las personas. (Fuente: https://olores.mma.gob.cl/efectos-en-salud/)
    p Algunos estudios sealan que los olores pueden afectar al estado psquico de las personas, influyendo negativamente sobre su estado anmico y pudiendo provocar situaciones de estrs, por esta razn, el olor es considerado un contaminante. Fuente: https://olores.mma.gob.cl/efectos-en-salud/)

    p En resumen, algunos sntomas negativos provocados por la molestia de olor son:
