    p.mt-3 Algunos de estos efectos dependen de la dosis y pueden aumentar con el tiempo.
    p La OMS seala que algunas sustancias tienen propiedades malolientes en concentraciones muy por debajo de aquellas en las que ocurren efectos txicos. As para la evaluacin de efectos sensoriales se han identificado tres umbrales: <strong>deteccin, reconocimiento y molestia.</strong>
