      h2 8.24 Procedimiento para la medicin de olores ofensivos
    p Para la medicin de olores ofensivos la resolucin 627 del ao 2006 establece como referencia el protocolo para la medicin de olores ofensivos en el cual establecen los procedimientos y caracterizacin de olores, en esta seccin veremos las condiciones generales de la medicin en campo en olores ofensivos.
    h5.mb-0 Caracterizacin 
    p Para caracterizar los olores ofensivos se pueden aplicar dos tcnicas: las sensoriales y las analticas.  en esta seccin realizaremos una descripcin de las tcnicas para su posterior seleccin dependiendo la fuente y el rea de influencia afectada.
    h5.mb-0 Las tcnicas sensoriales: 
    p se basan en la percepcin de los olores por el olfato humano. Tambin incluyen la determinacin del carcter de un olor (mapeo) y el nivel de agrado o desagrado de un olor (tono hednico) (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p. 33).
    .row.justify-content-center
      .col-lg-10
        .row.colort82.p-3.justify-content-center.mt-4
          h4 Las siguientes tcnicas son las ms empleadas en las mediciones de olores ofensivos.
          p.mt-3 <strong>Olfatometra Dinmica</strong>
          p En esta tcnica Se emplea el olfato humano como medio de captura y sensor de olor de esta manera se establece una metodologa para la determinacin del olor de una muestra gaseosa usando un panel panelistas como sensores para posteriormente determinar la emisin. El objetivo primordial de un estudio olfatomtrico es evaluar si las emisiones de una determinada instalacin pueden ser responsables o no de quejas en la poblacin vecina. A esta conclusin se llega con la interpretacin de los valores de concentracin de inmisin, habitualmente denominados mapas de olores. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p. 33).
