    h4.mt-5 Nomogramas
    p Los nomogramas son instrumentos grficos bidimensionales que permiten el clculo aproximado de una funcin. Los ms sencillos contienen dos ejes. En el eje horizontal se coloca la distancia a la que se estima que existe una determinada concentracin o a la que se esperara que haya molestias por olores y en el eje vertical se introduce el valor de emisin del olor (calculado partiendo de datos reales o estimados). (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.33).
    h4 Mediciones de Campo
    p Esta metodologa se basa en la observacin y deteccin del olor en campo, mediante observadores que se sitan en diferentes puntos de la zona en la que se quiere realizar la evaluacin de la molestia por olores. (La norma alemana VDI 3940 es la base de esta evaluacin y permite obtener la frecuencia de deteccin del olor)
    p Otra metodologa basada en la deteccin en campo es el uso de encuestas de olor y el clculo del ndice de molestia. Este mtodo, no slo tiene en cuenta la frecuencia de deteccin del olor, sino tambin la intensidad del olor (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.35).
    h4 Tablas FIDO
    p Esta herramienta se basa en el estudio de cuatro parmetros: la frecuencia, la duracin de la deteccin, la intensidad y el carcter agradable o desagradable del olor. Sirve para determinar si un olor se puede considerar molestia o no, esta metodologa se emplea en los EE. UU.  (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.35).
    .row.justify-content-center
      .col-lg-10
        .row.bg83.p-3
          .col-lg-2
          .col-lg-7.mt-5
            h5 Intensidad del olor
