              h5.mt-3 Olores no desagradables
              img(src='@/assets/curso/tema8/img52.svg', alt='Texto que describa la imagen')
    h5.mt-5 Tcnicas analticas
    p.mb-0 Las tcnicas analticas para caracterizacin de olores son relativamente fciles de aplicar e identifican cuantitativamente las especies qumicas presentes en un olor. La desventaja de la tcnica es que no provee informacin alguna referente a la molestia que puede generar un olor. Por esta razn, las tcnicas analticas son usadas para definir criterios de diseo de mtodos de control de olores (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.37).
    p  agrado o desagrado de un olor (tono hednico) (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p. 33).
    p Las siguientes son las tcnicas analticas ms empleadas para la deteccin de olores ofensivos.
    .row.mt-4.bgt85.p-4
      .col-lg-6.mt-5
        h5 Narices Electrnicas.
        p Las narices electrnicas son una serie de sensores de distintos materiales metlicos que reaccionan de forma distinta a un mismo componente qumico. Cuando una muestra olorosa entra en contacto con estos, se produce una respuesta qumica que se evala de forma conjunta para la totalidad de los sensores. La respuesta se relaciona con la intensidad o concentracin del olor a partir de los valores obtenidos por panelistas humanos que trasladan los resultados a una base de datos que queda registrada (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.37).

      .col-lg-5.mt-5
        img(src='@/assets/curso/tema8/img53.png', alt='Texto que describa la imagen')
    .row.mt-4.bgt85.p-4
      .col-lg-6.mt-5
        h5 Espectrmetros infrarrojos
        p En esta tcnica se emplean los Espectrmetros, en dcadas anteriores se han desarrollado mejores tecnologas para anlisis de compuestos qumicos, como el espectrmetro Transformador Infrarrojo de Paso Abierto de Fourier (Open-Path Fourier transforminfrared / OP-FTIR), el cual ofrece monitoreo remoto, anlisis multicompuestos rpidos y mediciones promedio. Esta tcnica ha sido aplicada exitosamente en la identificacin de fuentes de emisin.  (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.37).

      .col-lg-5.mt-5
        img(src='@/assets/curso/tema8/img54.png', alt='Texto que describa la imagen')
    .row.mt-4.bgt85.p-4
      .col-lg-11.mt-5
        h5 Modelizacin.
        p.mb-5 Es el mtodo ms empleado para valorar la contaminacin por olores generada por una fuente. Se basa en el uso de modelos matemticos de dispersin de contaminantes. La modelizacin permite evaluar el impacto odorfero que una instalacin, existente o futura, tiene o tendr sobre su entorno prximo. A partir de los datos meteorolgicos de la zona se puede efectuar la modelizacin. El resultado del modelo es la obtencin de la concentracin de olor en inmisin. Esta concentracin puede representarse mediante lneas con un mismo valor de concentracin de olor: las isodoras. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.38).
    .row.mt-4.justify-content-center
      .col-lg-10
        .row.p-4.colorn.align-items-center
          .col-lg-1
            img(src='@/assets/curso/tema3/1.svg', alt='Texto que describa la imagen')
          .col-lg-11
            h5 Repasemos lo aprendido!

            p.mb-0 En el siguiente documento audiovisual, repasaremos los conceptos aprendidos en esta seccin.
            p Olores.org (2014, julio 7) Formacin y emisin de olores: caracterizacin y evaluacin de impacto. E. P.ans, Odournethttps:
            a.anexo.mb-4.mb-lg-0.mt-3(href="https://en.wikipedia.org/wiki/Main_Page" target="_blank")
              .anexo__icono
                img(src="@/assets/template/icono-link.svg")
              .anexo__texto
                p Formacin y emisin de olores: caracterizacin y evaluacin de impacto. E. Pagans, Odournet.
    separador
    #t_8_25.titulo-segundo.color-acento-contenido
      h2 8.25 Medicin olfatometra dinmica
    .row
      .col-lg-6
        img(src='@/assets/curso/tema8/img55.png', alt='Texto que describa la imagen')

      .col-lg-6
        h5 Generalidades
        p La evaluacin de olores ofensivos a travs de mtodos sensoriales se basa en la determinacin de la concentracin del olor por olfatometra dinmica, metodologa desarrollada en la NTC 5880 Calidad del Aire. Determinacin de la concentracin del olor por olfatometra dinmica correspondiente con la Norma Europea EN 13725 estandarizada por el Comit Europeo de Normalizacin (CEN) (Protocolo Olores MADS p..31)
        p La norma establece un mtodo para la determinacin objetiva de la concentracin de olor de una muestra gaseosa usando olfatometra dinmica con evaluadores humanos y la velocidad de emisin de olores que emanan de fuentes puntuales y/o fuentes superficiales) 
        p As, el estndar es aplicable a la medida de la concentracin de olor de sustancias puras, mezclas definidas y mezclas indefinidas de sustancias olorosas gaseosas en aire o nitrgeno, usando olfatometra dinmica con un panel de evaluadores humanos que actan como sensor. (Protocolo Olores MADS p..31)
    p La unidad de medida es la unidad de olor europea por metro cbico: ouE/m3 La concentracin de olor se expresa en trminos de mltiplos del umbral de deteccin. Protocolo Olores MADS p..32)
    p En cuanto al campo de aplicacin la norma europea incluye:
    .row.justify-content-center
      .col-lg-10
        .row.colort82.p-4
          ul.lista-ul--color
            li 
              i.fas.fa-angle-right.colorclaro
              p  La medida de la concentracin msica del umbral de deteccin de las sustancias olorosas puras en g/m3 Protocolo Olores MADS p.32)
            li 
              i.fas.fa-angle-right.colorclaro
              p  La medida de la concentracin de olor de mezclas de sustancias olorosas en ouE/m3
            li 
              i.fas.fa-angle-right.colorclaro
              p  La medida de la velocidad de emisin de emisiones olorosas de fuentes puntuales y fuentes superficiales (con y sin un flujo hacia el exterior), incluyendo pre-dilucin durante el muestreo. Protocolo Olores MADS p.32
            li 
              i.fas.fa-angle-right.colorclaro
              p  El muestreo de sustancias olorosas de emisiones de alta humedad y temperatura (hasta 200C); Protocolo Olores MADS p.32)
    .row.mt-4.justify-content-center
      .col-lg-12
        .row.p-4.colorn
          .col-lg-1
            img(src='@/assets/curso/tema3/1.svg', alt='Texto que describa la imagen')
          .col-lg-11
            h5 Sabias que?


            p.mb-0 Sabias que la unidad con la que se establece la concentracin de olores se le conoce como OUE /m3

            p Unidad de Olor Europea, la cual se divide por una unidad de volumen (m3) Esta unidad es considerada una concentracin de olor, con la cual se establecen los estndares o niveles de inmisin de sustancias de olores ofensivos en Colombia.
            .row.justify-content-center.mt-4
              .col-lg-8
                .tabla-b.color-acento-contenido.mb-5
                  .tabla-b__header
                    h4 Metodos para la medicin de mezclas de sustancias segn el tipo de fuente
