    p.mt-3 Para resumir los mtodos aplicables para la medicin de mezclas de olores ofensivos, el protocolo de olores ofensivos del MADS, se sintetizan en la siguiente tabla:
    .row.mt-3.p-3.bgt86
      .col-lg-1
        img(src='@/assets/curso/tema8/img56.svg', alt='Texto que describa la imagen')
      .col-lg-1
      .col-lg-10
        h5 Toma de Muestra.
        p La primera actividad a realizar que es considerada de mayor importancia para abordar cualquier problema de malos olores, es LA IDENTIFICACIN DE LAS POSIBLES FUENTES de olor y las caractersticas de las emisiones de los olores ofensivos 
    p.mt-3 Cuando se realizan la toma de muestras para el anlisis por olfatometra dinmica, depende de la fuente se realiza los siguientes tipos de muestreos.
    .row.justify-content-center.mt-5
      .col-lg-10
        .row
          .col-lg-5
            img(src='@/assets/curso/tema8/img57.png', alt='Texto que describa la imagen')

          .col-lg-7
            .col-lg-10.colora2.p-2
              h4.text-white.mb-0.text-center Muestreo de fuentes puntuales  (Ductos o Chimeneas)
            .row.p-5.bgh1.mt-2
              p Se utiliza el mtodo de la sonda. Este mtodo consiste en la aspiracin mediante una sonda de una fraccin alcuota del gas emitido por la fuente.
        .row.mt-4
          .col-lg-5
            img(src='@/assets/curso/tema8/img58.png', alt='Texto que describa la imagen')

          .col-lg-7
            .col-lg-11.colora2.p-2
              h4.text-white.mb-0.text-center Muestreo de fuentes difusas aireadas  (superficiales activas). 
            .row.p-5.bgh1.mt-2
              p Para este tipo de fuentes (reactor biolgico de una depuradora o un biofiltro) se utiliza el mtodo de la Campana En estas situaciones se coloca una campana de base cuadrada (1m2) sobre la superficie a muestrear, de manera que, a la salida de esta, la muestra es succionada mediante una sonda.
              p Tomado de: Libro Snchez JI, Pea AA, Martnez JV, Valor I. Contaminacin Ambiental por Olores. Fundamentos Bsicos. Murcia: Calidad Ambiental; 2008. (P. 10)
        .row.mt-4
          .col-lg-5
            img(src='@/assets/curso/tema8/img59.png', alt='Texto que describa la imagen')

          .col-lg-7
            .col-lg-11.colora2.p-2
              h4.text-white.mb-0.text-center Muestreo de fuentes difusas no aireadas  (superficiales pasivas). 
            .row.p-5.bgh1.mt-2
              p Para este tipo de fuentes (decantador de una depuradora o una parva o pila de compost) se utiliza el mtodo del tnel del viento o cmara de flujo (conocida tambin como Caja Lindvall Para ello, mediante un soplante, se insufla un caudal conocido de aire previamente filtrado, a travs de la cmara de flujo, tomando la muestra mediante una sonda a la salida de esta.
    p.mt-3 En el caso de que, en cualquiera de las tcnicas de muestreo, se requiera llevar a cabo una dilucin en campo, el equipo debe cumplir con los mismos requisitos de exactitud y precisin que se aplican al olfatmetro. 
    p Por otro lado, durante el muestreo es necesario la medida de una serie de parmetros (temperatura, presin atmosfrica, caudal, humedad, etc.) que se utilizarn posteriormente para el clculo de la emisin de olor de cada foco. Para asegurar la calidad y fiabilidad de las medidas, los instrumentos de medida tambin deben pasar unas calibraciones peridicas y cumplir los correspondientes criterios de calidad.
    p Fuente: Libro Snchez JI, Pea AA, Martnez JV, Valor I. Contaminacin Ambiental por Olores. Fundamentos Bsicos. Murcia: Calidad Ambiental; 2008. (P. 10)
