    #t_8_26.titulo-segundo.color-acento-contenido
      h2 8.26 Mtodos para determinar la molestia
    .row 
      .col-lg-4
        img(src='@/assets/curso/tema8/img60.svg', alt='Texto que describa la imagen')
      .col-lg-8
        .row.p-5.bgh1.mt-2
          p.p-4 Por definicin cuando se hace referencia a olores ofensivos, necesariamente se estn describiendo dos condiciones:  la presencia de una o ms sustancias olorosas en el aire y un receptor sensible. En este contexto, es el receptor sensible quien acta como indicador de una situacin dada por olores ofensivos en el ambiente. La materializacin de dicha situacin se da en cuanto dicho receptor involucra a las Autoridades Ambientales a travs de una queja. (protocolo de olores MADS P..8)
    .col-lg-1.colora2.p-2.mt-4
      h4.text-white.mb-0.text-center Quejas
    p.mt-3 Las quejas son el medio por el cual, la comunidad puede manifestar su inconformidad por las molestias que generan los olores, sin embargo, para calificar una queja como vlida es necesario que se verifiquen algunos aspectos que le permitan a la autoridad ambiental determinar de manera objetiva la existencia real de una afectacin por olores ofensivos. (protocolo de olores MADS P..8)
    p As, con el propsito de establecer una herramienta que permita minimizar los posibles sesgos en las quejas, se aplica una encuesta estandarizada que corresponde con la NTC 6012-1. A continuacin se resume el contenido de la misma y los aspectos a tener en cuenta para su aplicacin. (protocolo de olores MADS P..8)
    p para realizar el procedimiento por atencin de quejas se establece el siguiente procedimiento.
