    #t_8_27.titulo-segundo.color-acento-contenido
      h2 8.2 NTC 6012-1 Efectos y evaluaciones de los olores
    h5 Planificacin de la encuesta.
    .row
      .col-lg-5
        img(src='@/assets/curso/tema8/img62.png', alt='Texto que describa la imagen')

      .col-lg-7
        h5.mb-0 Evaluacin sicomtrica de las molestias por olores.
        p Describe mtodos de investigacin para establecer los niveles de molestia existente, para lo cual incluye una poblacin control, determina herramientas para estimar reas de poblacin afectadas y comparar resultados de modelos de dispersin con la realidad descrita por la poblacin. 
        p El alcance de la NTC 6012-1 permite obtener un panorama objetivo de una presunta problemtica por olores, as como la evaluacin de la eficacia de las medidas tomadas para la prevencin y/o control de las emisiones. (protocolo de olores MADS P.9)
        p una vez definido el requerimiento, se procede a La planificacin de la encuesta se debe realizar con base en al menos una vista de campo en la que se verifique la existencia de una o ms potenciales actividades generadoras de olores ofensivos y el rea afectada identificando de manera aproximada el tamao de la poblacin en dicha rea. (protocolo de olores MADS P..8)
    h5.mt-4 Adicionalmente debe contar con los siguientes aspectos:
    .row.mt-3
      .col-lg-7
        .row.colort811.p-3
          ul.lista-ul--color
            li 
              i.fas.fa-angle-right.colorclaro
              p.mb-0  Respecto a la seleccin de la zona control (sin afectacin por olores ofensivos), entendiendo que se trata de una poblacin de caractersticas similares a la de la poblacin afectada (edad, estrato socio econmico, cobertura de servicios de saneamiento bsico, tiempo de residencia y permanencia en las viviendas entre otros)
            li 
              i.fas.fa-angle-right.colorclaro
              p.mb-0  Se debe definir el programa de aplicacin de las encuestas de tal modo que se cubra el rea definida
            li 
              i.fas.fa-angle-right.colorclaro
              p.mb-0 Se deben identificar los procesos que se realizan en dichas fuentes de emisin de olores ofensivos.
            li 
              i.fas.fa-angle-right.colorclaro
              p.mb-0 De la aplicacin de la encuesta se debe obtener informacin de los grados de molestia, las zonas afectadas, poblacin expuesta.
            li 
              i.fas.fa-angle-right.colorclaro
              p.mb-0 El personal encargado de la aplicacin de las encuestas debe tener experiencia interactuando con comunidades           


      .col-lg-5
        img(src='@/assets/curso/tema8/img63.png', alt='Texto que describa la imagen')
    p.mt-3 (protocolo de olores MADS P..8)
    p para establecer el procedimiento por atencin de quejas se establece el siguiente el siguiente procedimiento.
    TabsA.color-acento-contenido.mb-5
      .tarjeta.color-acento-botones--borde.p-4(titulo="Medicin de la molestia.")
        .row.justify-content-center
          .col-lg-11
            h4 Medicin de la molestia.
            p.mb-0 La NTC 6012-1 describe el modelo de cuestionario explicando el objetivo de cada una de las preguntas para la medicin de la molestia e informacin para el anlisis de los resultados de su aplicacin.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Seleccin del rea")
        .row.justify-content-center
          .col-lg-11
            h4 Seleccin del rea
            p La NTC 6012-1 define los parmetros bsicos para la seleccin del rea o zona de afectacin en la que se aplicar la encuesta. Entre los que se cuenta el flujo volumtrico de la fuente o actividad generadora. 
            p Esta informacin puede ser obtenida de referencias bibliogrficas considerando que se utiliza para hacer una estimacin preliminar de la zona. Algunas de las consideraciones ms relevantes a tener en cuenta para seleccionar la zona para la aplicacin de la encuesta son la velocidad y direccin del viento, la topografa y la nubosidad.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Muestreo y calidad del procedimiento de recoleccin de los datos.")
        .row.justify-content-center
          .col-lg-11
            h4 Seleccin del rea
            p La NTC 6012-1 define criterios que se deben tener en cuenta respecto a las caractersticas sociodemogrficas de la muestra a evaluar.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Evaluacin del cuestionario")
        .row.justify-content-center
          .col-lg-11
            h4 Evaluacin del cuestionario
            p La NTC 6012-1 describe en este numeral, para la escala de termmetro, la relacin matemtica entre los grados de molestia que se obtienen de las encuestas y las concentraciones de olor en los casos en los que la exposicin evaluada es uniforme en la zona de estudio.
      .tarjeta.color-acento-botones--borde.p-4(titulo="Anexos")
        .row.justify-content-center
          .col-lg-11
            h4 Anexos
            p Los anexos de la NTC6012-1 incluyen modelos para establecer contacto con los encuestados, ejemplos prcticos para la evaluacin de los datos obtenidos de la encuesta y el modelo de encuesta.
            p.mb-0 En caso de considerarlo necesario, el modelo de encuesta podr ser adicionado con ms preguntas en funcin de los objetivos que se planteen para la investigacin, los cuales debern analizarse
            p estadsticamente para obtener resultados representativos.  Este es un modelo de Encuesta de valoracin sicomtrica de molestia por Olores.
    .row.justify-content-center.mt-4
      .col-lg-10
        .row.p-4.bgt86
          .col-lg-6
            p 1. Qu tan sera considera usted la contaminacin general en ste rea de residencia?
            .row
              .col-lg-10
                img(src='@/assets/curso/tema8/x1.svg', alt='Texto que describa la imagen')
            p.mt-3 2. Cree usted que los residentes de esta va son molestados por la contaminacin ?
            .row
              .col-lg-10
                img(src='@/assets/curso/tema8/x2.svg', alt='Texto que describa la imagen')   
            p.mt-3 3.1. Qu tan fuertes son los olores fuera de la casa / en la calle?
